export default {
	data() {
	    return {	
			articles:{
				user_name:'',
				content:'',
				date:'',
				title:''
			},
	
			commitcontent:'',
			
	    }
	},
	
	onLoad:function(option){
		let th=this;
		wx.cloud.init()
		const db = wx.cloud.database();
		const _ = db.command;
		
		var artId=option.artId
		console.log(artId)
		
	    wx.cloud.callFunction({
		  name:'article',
		  data: {
		    $url:'artDetail',
		    other: {
			  id:artId
		    }
		  }
	    }).then(res => {
		  th.articles.user_name=res.result.data.user_name;
		  th.articles.content=res.result.data.content;
		  th.articles.date=res.result.data.date;
		  th.articles.title=res.result.data.title;
		  console.log(th.articles);
	    }).catch(err => {
		  console.error(err)
	    })
		
			
	},
	
	
	methods: {
		commitComments:function(){
			let content=this.commitcontent;
			const date = new Date();
			wx.cloud.callFunction({
				name:'article',
				data: {
				  $url:'publishArticle',
				  other: {
					user_name:'puber',
					user_avatar:'this is a imgurl',
					title:'ceshititle1',
					content:content,
					type:'ceshitype1',
					date:date
				  }
				}
			  }).then(res => {
				console.log(res)
				console.log(content);
				console.log(date);
			  }).catch(err => {
				console.error(err)
			  })
		}
	}
}
